<template>
  <div class="login">
    <v-content>
      <v-row>
        <v-col>
          <LoginForm @login="login" />
        </v-col>
      </v-row>
    </v-content>
  </div>
</template>

<script>
import LoginForm from "@/components/LoginForm.vue";
export default {
  data() {
    return {
      isHome: false
    };
  },
  methods: {
    login(credentials) {
      this.$store.dispatch("user/login", credentials).then(status => {
        if (status === 200) {
          this.$router.push({ name: "templates" });
        }
      });
    }
  },
  components: {
    LoginForm
  }
};
</script>

<style lang="scss" scoped></style>
