<template>
  <div>
    <v-card width="600" class="mx-auto mt-12 login">
      <v-card-title>
        <h1>Authentication</h1>
      </v-card-title>
      <v-card-text>
        <v-switch v-model="isAdminLogin" label="Login as an admin"></v-switch>
        <v-form @submit.prevent="validate" ref="form" v-model="valid">
          <v-text-field
            v-model="email"
            :rules="emailRules"
            type="email"
            label="Email"
            prepend-icon="mdi-account-circle"
          ></v-text-field>

          <v-text-field
            v-model="password"
            :rules="passwordRules"
            :type="showPassword ? 'text' : 'password'"
            label="Password"
            prepend-icon="mdi-lock"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
          ></v-text-field>

          <v-btn
            :disabled="!valid"
            class="primary mt-4"
            type="submit"
            rounded
            align="center"
            >Login</v-btn
          >
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isAdminLogin: false,
      showPassword: false,
      email: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+/.test(v) || "E-mail must be valid",
      ],
      password: "",
      passwordRules: [
        (v) => !!v || "Password is required",
        (v) => v.length >= 4 || "Password must be more than 5 characters",
      ],
      valid: false,
    };
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.login();
      }
    },
    login() {
      this.$emit("login", {
        isAdminLogin: this.isAdminLogin,
        email: this.email,
        password: this.password,
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
